<template>
<div id="content1">
	<h2  style="text-align: center;">登录界面</h2>
	<div id="content_tx">
  	<div class="tx">
    <a>用户名</a>
    <input class="user_info" id="username" type="text" name="user_name" v-model="user.user_name"/>
    </div>
    <div class="tx">
    <a>密&nbsp;&nbsp;&nbsp;&nbsp;码</a>
    <input class="user_info" id="userpsw" type="password" name="user_psw" v-model="user.user_psw"/>
    </div>
    <button id="login-btn" type="submit" v-on:click="do_signin">登录</button><br />
	{{user.user_name}}/{{user.user_psw}}
    </div><!-- content-tx -->
</div><!-- content1 -->
</template>

<script>
	export default{
		data: function() {
			return {
				user: {
					id: 0,
					user_name: "",
					user_psw: "",
				}
			}
		},
		created: function() {},
		methods:{
			do_signin: function() {	
				var self=this;
				self.$axios.get('NotebookServlet1', {
						params: self.user
					})
					.then(function(response) {
						console.log(response);
						if(response.data>0){
							self.$router.push({
								name: "itemadd",
								params: {
									user_id: response.data,
								},
							});
						}
						if(response.data<0){
							alert("1error");
						}
					})
					.catch(function(error) {
						if (error.request) {
							alert(error.request);
						}
						alert(error);
						console.log(error.message);
						console.log(error.config);
					});
			}   //do_signin()结束
		}  //methods结束
	} //export结束
</script>

<style>
	#content_tx{
		/*border:1px solid red;*/
		width:400px;
		height:200px;
		margin-left: 200px;
		margin-top: 100px;
		padding-top:60px;
			
		}
		.tx{
		/*border:1px solid black;*/
		width:300px;
		height:40px;
		margin-bottom:10px;
		margin-left:30px;
		padding:2px;
		}
		.tx a{
			color:#1E90FF;
		}
		.user_info{
		width:200px;
		height:15px;
		border-radius:10px;
		margin-left:3px;
		border:1px solid dodgerblue;
		padding:10px;
		}
		#login-btn{
		width:80px;
		border-radius:5px;
		background-color: #1E90FF;
		color:white;
		margin-left:150px;
		margin-bottom: 10px;
		}
</style>
