<template>
<div id="content2">
	<h2  style="text-align: center;">新增列表</h2>
	<label style="text-align:center;">当前用户id为:{{this.$route.params.user_id}}</label>
  	<div class="content_item">
  		<div class="item">
  			<a>支出项目</a>
  			<input class="info" type="text" name="item_text"  v-model="item.item_text"/>
  		</div>
  	    <div class="item">
  	    	<a>支出金额</a>
  			<input class="info" type="text" name="amount" v-model="item.amount"/>
  		</div>
  	    <div class="item">
  	    	<a>支出分类</a>
  			<select class="info" name="category_id" v-model="item.category_id">
  				<option>-请选择-</option>
  				<option>1</option>
  				<option>2</option>
  				<option>3</option>
  				<option>4</option>
  			</select>
  		</div>
  	    <div class="item">
  	    	<a>支出日期</a>
  			<input class="info" type="Date" name="date_paid" v-model="item.date_paid"/>
  		</div>
  	<button id="add-btn" v-on:click="do_itemadd">新增</button>
  	</div><!--content_item-->
    </div><!-- content2 -->
</template>

<script>
	export default{
		data: function() {
			return {
				item: {
					item_text:"",
					amount:"",
					category_id:"",
					date_paid:"",	
					user_id:"",
				}
			}
		},
		created: function() {},
		methods:{
			do_itemadd: function() {	
				var self=this;
				let p = self.$route.params
				if(Object.keys(p).length) {
				  self.item.user_id = p.user_id
				}
				self.$axios.get('NotebookServlet2', {
						params: self.item,
					})
					.then(function(response) {
						console.log(response);
						if(response.data>0){
							self.$router.push({
								name:'itemlist',
								params: {
									user_id: self.item.user_id,
								},
							});
						}
					})
					.catch(function(error) {
						if (error.request) {
							alert(error.request);
						}
						alert(error);
						console.log(error.message);
						console.log(error.config);
					});
			}   //do_itemadd()结束
		}  //methods结束
}
</script>

<style>
	#content2{
	width:800px;
	height:500px;
	}
	.content_item{
		/*border:1px solid red;*/
		width:500px;
		height:300px;
		margin-left:150px;
		margin-top:60px;
		padding-top:60px;
			
		}	
		.item{
		/*border:1px solid black;*/
		width:300px;
		height:40px;
		margin-left: 70px;
		margin-bottom: 10px;
		padding:2px 2px;
		}
		.item a{
		color:#0000FF;
		}
		.info{
		border:1px solid blue;
		width:200px;
		height:15px;
		height:25px;
		border-radius:10px;
		margin-left:4px;
		}
		#add-btn{
		width:70px;
		height:25px;
		margin-left:190px;
		border-radius:8px;
		letter-spacing: 3px;
		background-color:#1E90FF;
		color:white;
		}
</style>
