<template>
	<div id="tables">
		<h2 style="text-align: center;">记账列表</h2>
		<label style="text-align:center;">当前用户id为:{{this.$route.params.user_id}}</label>
		<div id="table1">
		<table border="1">
		<tr>
			<td style="width:100px">序号</td>
			<td style="width:100px">ID</td>
			<td style="width:100px">支出项目</td>
			<td style="width:100px">支出金额</td>
			<td style="width:100px">记账日期</td>
			<td style="width:100px">记账类别</td>
			<td style="width:120px">操作</td>
		</tr>
		</table>
		</div><!-- table1 -->
	<div id="list">
		<div id="table2" v-for="(note,index) in notes" v-bind:key="note.id">
		<table border="1">
		<tr>
			<td style="width:100px">{{index+1}}</td>
			<td style="width:100px">{{note.id}}</input></td>
			<td style="width:100px"><input type="text" style="width:90px;border:0px" v-model="note.item_text"></input></td>
			<td style="width:100px"><input type="text" style="width:90px;border:0px" v-model="note.amount"></input></td>
			<td style="width:100px"><input type="text" style="width:90px;border:0px" v-model="note.date_paid"></input></td>
			<td style="width:100px">{{note.category_text}}</td>
			<td style="width:120px">
			<button type="button"  v-on:click="do_update(note.id,note.item_text,note.amount,note.date_paid,note.category_text)" class="btn btn-info">修改</button>
			<button type="button" v-on:click="do_delete(note.id)" class="btn btn-warning">删除</button>
			</td>
		</tr>	
		</table>
		</div>	<!--table2-->
		<div><button v-on:click="do_loading()">刷新</button></div><!-- button -->
		<br />user_id:{{this.$route.params.user_id}}
	</div>	<!-- app-->
	</div><!-- tables -->
</template>

<script>
	export default{
		data: function() {
			return {
				notes:[],
				id_info:{
					id:"",
					user_id:"1",
				},
				update_info:{
					item_text:"",
					amount:"",
					date_paid:"",
					category_text:"",
				}
			}
		},
		created: function() {},
		methods:{
			do_loading: function() {	
				var self=this;
				let p = self.$route.params
				if(Object.keys(p).length) {
				  self.id_info.user_id = p.user_id
				}
				self.$axios.get('NotebookServlet3', {
						params:{
							user_id:self.id_info.user_id,
						}
					})
					.then(function(response) {
						self.notes=response.data;
						console.log(response);
					})
					.catch(function(error) {
						if (error.request) {
							alert(error.request);
						}
						alert(error);
						console.log(error.message);
						console.log(error.config);
					});
			}   ,//do_loading()结束
			do_delete:function(id){
				var self=this;
				self.id_info.id=id;
				let p = self.$route.params
				if(Object.keys(p).length) {
				  self.id_info.user_id = p.user_id;
				}
				self.$axios.get('NotebookServlet4', {
						params:self.id_info
					})
					.then(function(response) {
						alert("删除id="+id+"成功");
						self.notes=response.data;
					})
					.catch(function(error) {
						if (error.request) {
							alert(error.request);
						}
						alert(error);
						console.log(error.message);
						console.log(error.config);
					});
			},//do_delete()结束
			do_update:function(id,item_text,amount,date_paid,category_text){
				var self=this;
				self.update_info.amount=amount;
				self.id_info.id=id;
				self.update_info.date_paid=date_paid;
				self.update_info.item_text=item_text;
				let p = self.$route.params
				if(Object.keys(p).length) {
				  self.id_info.user_id = p.user_id;
				}
				self.$axios.get('NotebookServlet5', {
						params:{
							id:self.id_info.id,
							user_id:self.id_info.user_id,
							item_text:self.update_info.item_text,
							amount:self.update_info.amount,
							date_paid:self.update_info.date_paid,
							}
					})
					.then(function(response) {
						alert("修改id="+id+"成功");
						self.notes=response.data;
						console.log(response);
					})
					.catch(function(error) {
						if (error.request) {
							alert(error.request);
						}
						alert(error);
						console.log(error.message);
						console.log(error.config);
					});
			},//do_update()结束
			
		}  ,//methods结束
		mounted:function(){
			var self=this;
			let p = self.$route.params
			if(Object.keys(p).length) {
			  self.id_info.user_id = p.user_id
			}
			self.$axios.get('NotebookServlet3', {
					params:{
						user_id:self.id_info.user_id,
					}
				})
				.then(function(response) {
					self.notes=response.data;
					console.log(response);
				})
				.catch(function(error) {
					if (error.request) {
						alert(error.request);
					}
					alert(error);
					console.log(error.message);
					console.log(error.config);
				});
			}//mounted结束

}
</script>


<style>
</style>
