import Vue from 'vue'
import VueRouter from 'vue-router'
import Vuex from 'vuex'
import axios from 'axios'
import App from './App.vue'
import TheUserSignin from './pages/TheUserSignin.vue'
import TheItemAdd from './pages/TheItemAdd.vue'
import TheItemList from './pages/TheItemList.vue'


Vue.config.productionTip = false
Vue.use(VueRouter)
axios.defaults.baseURL='http://192.168.179.128:8080/07/'
Vue.prototype.$axios=axios
const routes = [{
		name:'usersignin',
		path: '/usersignin',
		component: TheUserSignin
	},
	{
		path: '/',
		redirect:'/usersignin'
	},
	{
		name:'itemadd',
		path: '/itemadd',
		component: TheItemAdd
	},
	{
		name:'itemlist',
		path: '/itemlist',
		component: TheItemList
	}
]

const router = new VueRouter({
	mode: "history",
	routes: routes
})

Vue.use(Vuex);
const store = new Vuex.Store({
	state: {
	},
	mutations: {
		// mutation接收两个参数，第一个参数永远是state，第二个参数是一个对象，payload负载

		},
});
new Vue({
	router: router,
	store:store,
	render: h => h(App),
}).$mount('#app')
